// Function to load external CSS
function loadStyleSheet(href) {
  const link = document.createElement("link");
  link.rel = "stylesheet";
  link.href = href;
  document.head.appendChild(link);
}

// Function to create the OTP form dynamically
export function createOtpForm(parent) {
  // Create the container div
  loadStyleSheet("https://unpkg.com/boxicons@2.1.4/css/boxicons.min.css");

  const container = document.createElement("div");
  container.classList.add("container");

  // Create the header
  const header = document.createElement("header");
  const icon = document.createElement("i");
  icon.classList.add("bx", "bxs-check-shield");
  header.appendChild(icon);
  container.appendChild(header);

  // Create the h4 element
  const h4 = document.createElement("h4");
  h4.textContent = "Enter OTP Code";
  container.appendChild(h4);

  // Create the form element
  const form = document.createElement("form");
  form.setAttribute("action", "#");

  // Create the input field div
  const inputField = document.createElement("div");
  inputField.classList.add("input-field");

  // Create the 4 input elements
  for (let i = 0; i < 4; i++) {
    const input = document.createElement("input");
    input.setAttribute("type", "number");
    if (i !== 0) input.setAttribute("disabled", true); // Disable all except the first input
    inputField.appendChild(input);
  }

  // Append the input field div to the form
  form.appendChild(inputField);

  // Create the button element
  const button = document.createElement("button");
  button.textContent = "Verify OTP";
  form.appendChild(button);

  // Append the form to the container
  container.appendChild(form);

  // JavaScript to manage input behavior
  const inputs = container.querySelectorAll("input"),
    verifyButton = container.querySelector("button");

  inputs.forEach((input, index1) => {
    input.addEventListener("keyup", (e) => {
      const currentInput = input,
        nextInput = input.nextElementSibling,
        prevInput = input.previousElementSibling;

      if (currentInput.value.length > 1) {
        currentInput.value = "";
        return;
      }

      if (nextInput && nextInput.hasAttribute("disabled") && currentInput.value !== "") {
        nextInput.removeAttribute("disabled");
        nextInput.focus();
      }

      if (e.key === "Backspace") {
        inputs.forEach((input, index2) => {
          if (index1 <= index2 && prevInput) {
            input.setAttribute("disabled", true);
            input.value = "";
            prevInput.focus();
          }
        });
      }

      if (!inputs[3].disabled && inputs[3].value !== "") {
        verifyButton.classList.add("active");
        return;
      }
      verifyButton.classList.remove("active");
    });
  });

  window.addEventListener("load", () => inputs[0].focus());

  // Return the main container so it can be used elsewhere
  parent.appendChild(container);
}
